@import "../../variables.module.scss";

.container {
  margin: 0 2px 30px 2px;
  color: $color-secondary;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.firstLine {
  font-size: 34px;
  font-family: $font-primary;
  margin: 20px 0 -46px 0;
  height: 72px;
  text-align: center;
  position: relative;
  z-index: 2;
}

.secondLine {
  font-size: 24.5px;
  font-family: $font-secondary;
  text-align: center;
  width: 230px;
}

@media screen and (min-width: $laptop) {
  .container {
    margin: 0 0 0 30px;
    flex-basis: 500px;
    justify-content: center;
  }

  .firstLine {
    font-size: 2.5em;
    height: 53px;
  }

  .secondLine {
    font-size: 1.6em;
  }
}
